import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Card, CardActionArea, CardMedia, CardActions, CardContent, Typography } from '@material-ui/core';
import RMSubForm from './rm/rm-sub-form';
import PMSubForm from './compt_mgt/lm-sub-form';
import UpdateForm from './components/update-form';
import AllStaff from './components/all-staff';



const Subscription = () => {

    const [pmForm, setPMForm] = useState(false);
    const [rmform, setrmform] = useState(false);
    const [updateModal, setUpdateModal] = useState(false);
    const [service, setService] = useState()
    const [rmSub, setRMSub] = useState('')
    const [lmDetails, setLMDetails] = useState()
    const [company, setCompany] = useState()

    const [show, setShow] = useState(false);

    useEffect(() => {
        const company = JSON.parse(sessionStorage.getItem('key'));
        setCompany(company);

        setRMSub(company.sub_status_rm)

        // axios.get(`http://localhost:5000/corporatesubscriptions/${company.id}`)
        //     .then(res => {
        //         if (res.data.length > 0) {
        //             if(res.data.sub_status_rm === 'active')
        //                 setRMDetails(true);
        //         }
        //     }).catch(err => console.log(err))


    }, []);

    const closeModal = (status = false) => {
        setPMForm(false);
        setrmform(false);
        setUpdateModal(false);
        if (status === 'active') {
            setRMSub('active')
        }

    }

    const getRMDetails = () => {

        setService("RM");
        setUpdateModal(true)
    }

    const getLMdetails = () => {

        setService('LM')
        setUpdateModal(true)
    }




    return (
        <>
            <div className="container mt-5">
                <div className="row justify-content-center">

                    <div className="col col-lg-4 col-sm-6">
                        <br />
                        <Card className="">
                            <CardActionArea>
                                <CardMedia
                                    component="img"
                                    alt="Contemplative Reptile"
                                    height="140"
                                    image="data:image/jpeg;base64,"
                                    title="Contemplative Reptile"
                                />
                                <CardContent onClick={getRMDetails}>
                                    <Typography gutterBottom variant="" component="h5">
                                        Corporate Recruitment  Management
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        Widespread group of squamate reptiles, with over 6,000 species, ranging
                                        across all continents except Antarctica
                                    </Typography>
                                </CardContent>
                            </CardActionArea>
                            <CardActions>
                                {rmSub === 'active' ?
                                    <button
                                        className="btn mt-3 py-2 border-0"
                                        style={{ background: '#97BA0C', color: 'white' }}
                                        disabled
                                    >
                                        Subscribed
                                </button>
                                    :
                                    <button
                                        className="btn mt-3 py-2 border-0"
                                        style={{ background: '#21A5E7', color: 'white' }}
                                        onClick={() => setrmform(!rmform)}
                                    >
                                        Subscribe
                                </button>}
                            </CardActions>
                        </Card>
                    </div>
                    <div className="col col-lg-4 col-sm-6">
                        <br />
                        <Card className="">
                            <CardActionArea>
                                <CardMedia
                                    component="img"
                                    alt="Contemplative Reptile"
                                    height="140"
                                    image="https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSlDxYMQX6TQvAHVADOD5FEsrrV6iqAguLxYIVRZ6Y42B-5tV1R"
                                    title="Contemplative Reptile"
                                />
                                <CardContent onClick={getLMdetails}>
                                    <Typography gutterBottom variant="" component="h5">
                                        {/* Corporate Leadership <br /> Management */}
                                        Personnel Competence Management
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        Widespread group of squamate reptiles, with over 6,000 species, ranging
                                        across all continents except Antarctica
                                    </Typography>
                                </CardContent>
                            </CardActionArea>
                            <CardActions>
                                {lmDetails ?
                                    <button
                                        className="btn font-weight-bold mt-3 py-2 border-0"
                                        style={{ background: '#97BA0C', color: 'white' }}
                                        disabled={lmDetails}
                                    >
                                        Subscribed
                                </button>
                                    :
                                    <button
                                        className="btn font-weight-bold mt-3 py-2 border-0"
                                        style={{ background: '#21A5E7', color: 'white' }}
                                        onClick={() => setPMForm(!pmForm)}
                                    >
                                        Subscribe
                                </button>}
                            </CardActions>
                        </Card>
                    </div>
                </div>
            </div>
            <PMSubForm show={pmForm} onHide={() => setPMForm(!pmForm)} closeModal={closeModal} />
            <RMSubForm show={rmform} onHide={() => setrmform(!rmform)} closeModal={closeModal} />
            <UpdateForm
                show={updateModal}
                onHide={() => setUpdateModal(!updateModal)}
                service={service}
                closeModal={closeModal}

            />
            <AllStaff show={show} onHide={() => setShow(!show)} company={company} closeModal={closeModal} />
        </>
    );
}
export default Subscription;